.reviews {
  position: relative;
  z-index: 10;
  display: grid;
  grid-template-columns: minmax(20px, 1fr) repeat(12, 1fr) minmax(20px, 1fr);
  margin-bottom: 25px;
}

.reviews__title {
  grid-column: -2 / 2;
  margin: 0;
  margin-bottom: 10px;
  font-size: 15px;
  letter-spacing: $ls_main;
  text-align: center;
  text-transform: uppercase;
}

.reviews__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column: -2 / 2;
  grid-gap: 15px;
}

@media (min-width: $mobile_l_width) {
  .reviews {
    grid-template-columns: minmax(30px, 1fr) repeat(12, 1fr) minmax(30px, 1fr);
  }
}

@media (min-width: $tablet_secondary_width) {
  .reviews__list {
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
  }
}

@media (min-width: $tablet_main_width) {
  .reviews__list {
    grid-gap: 25px;
  }
}

@media (min-width: $desktop_width) {
  .reviews {
    position: relative;
    z-index: 10;
    display: grid;
    grid-template-columns: minmax(30px, 1fr) repeat(12, 1fr) minmax(30px, 1fr);
    margin-bottom: 32px;
  }

  .reviews__title {
    margin-bottom: 17px;
    font-size: $font_size_title;
  }

  .reviews__list {
    grid-template-columns: repeat(9, 1fr);
    grid-gap: 30px;
  }
}
