.banner {
  display: grid;
  grid-template-columns: minmax(20px, 1fr) repeat(12, 1fr) minmax(20px, 1fr);
  margin-bottom: 25px;
  padding-bottom: 35px;
  color: $color_white;
  background-image: url('../img/banner-mask.jpg');
  background-color: #00182a;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
}

.banner__title {
  position: relative;
  grid-column: 2 / -2;
  grid-row: 2;
  margin: 0;
  margin-bottom: 50px;
  padding-top: 55px;
  font-size: 34px;
  font-weight: $font_weight_normal;
  letter-spacing: $ls_main;
  text-align: center;
  text-transform: uppercase;

  &::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 50%;
    width: 35px;
    height: 35px;
    background-image: url("../img/banner-icon.svg");
    background-size: 100% 100%;
    transform: translateX(-50%);
  }
}

.banner__new {
  display: none;
}

.banner__text {
  grid-column: 3 / -3;
  grid-row: 3;
  margin: 0;
  text-align: center;
}

@media (min-width: $tablet_main_width) {
  .banner__new {
    display: block;
    grid-column: 2 / -2;
    grid-row: 1;
    justify-self: flex-start;
    padding: 0 26px;
    font-size: 16px;
    letter-spacing: $ls_main;
    text-transform: uppercase;
    background-color: $color_hover;
  }

  .banner__title {
    font-size: 50px;
  }

  .banner__text {
    grid-column: 4 / -4;
    padding: 0px 55px;
  }
}

@media (min-width: $desktop_width) {
  .banner {
    grid-template-columns: minmax(140px, 1fr) repeat(12, 1fr) minmax(140px, 1fr);
    margin-bottom: 42px;
    padding-bottom: 51px;
    // background-image: url('../img/banner-mask.jpg');
    // background-repeat: no-repeat;
    // background-position: center;
    background-size: 100% 100%;
  }

  .banner__title {
    grid-column: 2 / -2;
    grid-row: 2;
    margin-bottom: 140px;
    padding-top: 115px;
    font-size: 72px;
    line-height: 1;

    &::before {
      top: 21px;
      width: 55px;
      height: 55px;
    }
  }

  .banner__text {
    grid-column: 5 / -5;
    padding: 0px 75px;
  }
}
