.footer {
  position: relative;
  z-index: 1;
  display: grid;
  grid-template-columns: minmax(20px, 1fr) repeat(12, 1fr) minmax(20px, 1fr);
  padding: 15px 0 10px;
  background-color: #575757;
}

.footer__logo {
  grid-column: 2 / -2;
  margin-bottom: 15px;
}

.footer__nav {
  margin-bottom: 20px;
}

.footer__social {
  padding-top: 25px;
}

.footer .logo {
  @include font($font_family_main, 25px, $font_weight_black);
  color: $color_white;
}

.footer .site-nav {
  display: block;
  grid-column: 2 / -2;
  grid-row: 2;
}

.footer .site-nav__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 10px;
}

.footer .site-nav__link {
  color: $color_white;

  &:hover,
  &:focus {
    color: $color_hover;
  }

  &:active {
    color: rgba($color_hover, 0.6);
  }
}

@media (min-width: $mobile_l_width) {
  .footer .site-nav__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: $tablet_main_width) {
  .footer {
    padding: 20px 0 10px;
  }

  .footer__logo {
    margin-bottom: 23px;
  }

  .footer__nav {
    margin-bottom: 25px;
  }

  .footer .site-nav__list {
    grid-template-columns: repeat(4, 1fr);
  }

  .footer__social {
    padding-top: 42px;
  }
}

@media (min-width: $desktop_width) {
  .footer {
    grid-template-columns: minmax(140px, 1fr) repeat(12, 1fr) minmax(140px, 1fr);
    padding: 24px 0 10px;
  }

  .footer__logo {
    margin-bottom: 28px;
  }

  .footer .logo {
    font-size: 28px;
  }

  .footer .site-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .footer__nav {
    margin-bottom: 44px;
  }
}
