.social {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-column: 2 / -2;
  grid-row: 3;
  border-top: 1px solid rgba(255, 255, 255, 0.4);
}

.social__link {
  display: flex;
  width: 35px;
  height: 35px;
  background-color: #fff;

  path {
    transition: all 0.2s ease-in;
  }

  &:hover path,
  &:focus path {
    fill: $color_hover;
  }

  &:active path {
    fill: rgba($color_hover, 0.6);
  }
}

.social__svg {
  margin: auto;
}

.social__item {
  &:not(:last-child) {
    margin-right: 15px;
  }
}

@media (min-width: $tablet_main_width) {
  .social__link {
    width: 45px;
    height: 45px;
  }
}
