.logo {
  display: inline-block;
  // grid-column: 1 / -1;
  justify-self: center;
  color: $color_black;
  @include font($font_family_main, 25px, $font_weight_black);
  letter-spacing: 1px;
  vertical-align: middle;
  transition: all 0.2s ease-in;

  &:hover,
  &:focus {
    color: rgba($color_black, 0.6);
    opacity: 0.6;
  }
}

@media (min-width: $desktop_width) {
  .logo {
    font-size: 35px;
  }
}
