.season-ticket {
  position: relative;
  max-width: 400px;

  &:not(:last-of-type)::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0;
    width: 100px;
    height: 1px;
    background-color: rgba(0, 0, 0, 0.1);
    transform: translateX(-50%);
  }
}

.season-tisket__title {
  margin: 0;
  margin-bottom: 10px;
}

.season-ticket__link {
  @include font($font_family_main, 17px, $font_weight_black);
  color: $color_black;
  transition: all 0.2s ease-in;

  &:hover,
  &:focus {
    color: $color_hover;
  }

  &:active {
    color: rgba($color_hover, 0.6);
  }
}

.season-ticket__desc {
  margin: 0;
  text-align: center;
}

@media (min-width: $tablet_main_width) {
  .season-ticket {
    &:not(:last-of-type)::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 100px;
      height: 1px;
      background-color: rgba(0, 0, 0, 0.1);
      transform: translateX(-50%);
    }
  }

  .season-tisket__title {
    margin-bottom: 5px;
  }

  .season-ticket__link {
    font-size: 23px;
  }
}
