.btn {
  position: relative;
  display: inline-block;
  padding: 12px 30px;
  font-size: 10px;
  font-weight: $font_weight_bold;
  letter-spacing: $ls_main;
  color: $color_white;
  text-transform: uppercase;
  vertical-align: middle;
  background-color: $color_hover;
  cursor: pointer;
}


@media (min-width: $tablet_main_width) {
  .btn {
    padding: 15px 30px;
    font-size: 12px;
  }
}
