.tickets {
  display: grid;
  grid-template-columns: minmax(20px, 1fr) repeat(12, 1fr) minmax(20px, 1fr);
  margin-bottom: 25px;
  background-color: #ffffff;
}

.tickets__wrap {
  grid-column: 2 / -2;
  padding: 10px 0;
  text-align: center;
}

.tickets__title {
  margin: 0;
  margin-bottom: 15px;
  font-size: 15px;
  letter-spacing: $ls_main;
  text-transform: uppercase;
}

.tickets__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.tickets__item {
  margin-bottom: 13px;
  padding-bottom: 14px;
}

.tickets__btn-more {
  position: relative;
  margin-top: 2px;
  padding-right: 40px;

  &::after {
    content: "";
    position: absolute;
    top: 48%;
    right: 15px;
    width: 20px;
    height: 20px;
    background-image: url("../img/arrow-right.svg");
    background-repeat: no-repeat;
    background-position: center;
    transform: translate(0, -50%) scale(0.8);
    transition: all 0.2s ease-in;
  }

  &:hover::after,
  &:focus::after {
    transform: translate(10px, -50%) scale(0.6);
  }
}

@media (min-width: $tablet_main_width) {
  .tickets {
    grid-template-columns: minmax(30px, 1fr) repeat(12, 1fr) minmax(30px, 1fr);
    margin-bottom: 35px;
    background-color: #f7f7f7;
  }

  .tickets__wrap {
    position: relative;
    z-index: 1;
    grid-column: 4/-4;
    padding: 20px 0;

    &::before {
      content: "";
      position: absolute;
      top: -50px;
      left: 50%;
      z-index: -1;
      width: 710px;
      height: 710px;
      border-radius: 50%;
      background-color: #ffffff;
      transform: translateX(-50%);
    }
  }

  .tickets__title {
    margin-bottom: 25px;
    font-size: 17px;
  }

  .tickets__item {
    margin-bottom: 18px;
    padding-bottom: 19px;
  }

  .tickets__btn-more {
    padding-right: 60px;

    &::after {
      top: 49%;
      right: 31px;
      transform: translate(0, -50%) scale(1);
    }

    &:hover::after,
    &:focus::after {
      transform: translate(10px, -50%) scale(0.8);
    }
  }
}

@media (min-width: $desktop_width) {
  .tickets {
    grid-template-columns: minmax(140px, 1fr) repeat(12, 1fr) minmax(140px, 1fr);
    margin-bottom: 51px;
  }

  .tickets__wrap {
    padding: 34px 0;
  }

  .tickets__title {
    margin-bottom: 32px;
  }

  .tickets__item {
    margin-bottom: 23px;
    padding-bottom: 24px;
  }
}
