// Шрифты
$font_family_main: 'Playfire';
$font_family_secondary: Georgia;
$font_size_base: 14px;
$font_size_title: 17px;
$line_height_base: 1.5;
$ls_main: 2px;
$font_weight_light: 300;
$font_weight_normal: 400;
$font_weight_medium: 500;
$font_weight_bold: 700;
$font_weight_black: 900;

// Разрешения
$mobile_main_width: 320px;
$mobile_l_width: 480px;
$tablet_main_width: 768px;
$tablet_secondary_width: 600px;
$notebook_width: 1024px;
$desktop_width: 1150px;
// $desktop_width: 1440px;
$mobile_width_only: 767px;
$retina_dpi: 144dpi;
$retina_dppx: 1.5dppx;

// Цвета
$color_white: #ffffff;
$color_black: #000000;
$color_hover: #ff7931;

