.header {
  display: grid;
  // grid-template-columns: repeat(12, 1fr);
  grid-template-columns: minmax(20px, 1fr) repeat(12, 1fr) minmax(20px, 1fr);
  padding: 30px 0;
  // grid-template-areas:
  //   "logo logo logo logo logo logo logo logo logo logo logo logo",
  //   "site-nav site-nav site-nav site-nav site-nav site-nav site-nav site-nav site-nav site-nav site-nav site-nav";
}

.header__logo {
  grid-column: 1 / -1;
  margin-bottom: 15px;
}

@media (min-width: $mobile_l_width) {
  .header {
    grid-template-columns: minmax(30px, 1fr) repeat(12, 1fr) minmax(30px, 1fr);
  }

  .header__logo {
    grid-column: 2 / 10;
    grid-row: 1;
  }
}

@media (min-width: $tablet_main_width) {
  .header {
    grid-template-columns: minmax(70px, 1fr) repeat(12, 1fr) minmax(70px, 1fr);
  }

  .header__logo {
    grid-column: 2 / -2;
    font-size: 30px;
    margin-bottom: 25px;
  }
}

@media (min-width: $desktop_width) {
  .header {
    grid-template-columns: minmax(140px, 1fr) repeat(12, 1fr) minmax(140px, 1fr);
    padding: 58px 0 45px;
  }

  .header__logo {
    margin-bottom: 41px;
  }
}
