.site-nav {
  position: relative;
  grid-column: 1 / -1;
  grid-row: 3;
  margin-top: 25px;
}

.site-nav__toggle {
  position: absolute;
  top: -30px;
  display: block;
  width: 100%;
  height: 22px;
  padding: 0;
  border: 0;
  background-color: transparent;
  outline: none;
  cursor: pointer;

  &:hover::before,
  &:hover::after {
    opacity: 0.5;
  }
}

.site-nav--closed .site-nav__toggle::before {
  content: "";
  position: absolute;
  top: 5px;
  left: 50%;
  width: 20px;
  height: 1px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000, 0 12px 0 0 #000;
  transform: translateX(-50%);
}

.site-nav--opened .site-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 1px;
    background-color: #000000;
  }

  &::before {
    transform: translate(-50%,-50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%,-50%) rotate(-45deg);
  }
}

.site-nav__list {
  display: flex;
  flex-direction: column;
  // justify-content: space-between;
  // flex-wrap: wrap;
}

.site-nav__link {
  display: block;
  padding: 10px 0;
  font-size: 13px;
  letter-spacing: $ls_main;
  color: $color_black;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.2s ease-in;

  &:hover,
  &:focus {
    color: $color_hover;
  }

  &:active {
    color: rgba($color_hover, 0.6);
  }
}

@media (min-width: $mobile_l_width) {
  .site-nav {
    grid-column: 2 / -2;
  }

  .site-nav__toggle {
    top: -66px;
    right: 10%;
    width: auto;
  }

  .site-nav__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: $mobile_width_only) {
  .site-nav--closed .site-nav__item {
    display: none;
  }

  .site-nav--nojs .site-nav__item {
    display: block;
  }

  .site-nav--nojs .site-nav__toggle {
    display: none;
  }
}

@media (min-width: $tablet_main_width) {
  .site-nav__toggle {
    display: none;
  }

  .site-nav__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: $notebook_width) {
  .site-nav__list {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (min-width: $desktop_width) {
  .site-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .site-nav__link {
    display: inline-block;
    padding: 0;
    font-size: 15px;
  }
}
