.review-card {
  grid-column: span 3;

  &--large {
    grid-column: 1 / -1;
  }
}

.review-card__img {
  position: relative;
  display: block;
  height: 205px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    display: block;
    width: 100%;
    height: 100%;
    background-color: transparent;
    pointer-events: none;
    transition: all 0.2s ease-in;
  }

  &:hover::before,
  &:focus::before {
    background-color: rgba(0, 0, 0, 0.2);
  }
}

.review-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.review-card__info {
  padding: 10px 0;
}

.review-card__title {
  margin: 0;
  margin-bottom: 4px;

  a {
    @include font($font_family_main, 17px, $font_weight_black);
    color: $color_black;
    transition: all 0.2s ease-in;
  }

  &:hover a,
  &:focus a {
    color: $color_hover;
  }

  &:active a {
    color: rgba($color_hover, 0.6);
  }
}

.review-card__desc {
  margin: 0;
  padding-right: 20px;
  line-height: $line_height_base;
}

@media (min-width: $tablet_secondary_width) {
  .review-card {
    &--large {
      display: flex;
      justify-content: space-between;

      .review-card__info {
        padding-top: 0;
      }
    }
  }

  .review-card__img {
    flex-basis: 30%;
  }

  .review-card__title {
    margin-bottom: 7px;

    a {
      font-size: 20px;
    }
  }

  .review-card__info {
    flex-basis: 65%;
  }
}

@media (min-width: $desktop_width) {
  .review-card {
    grid-column: span 3;

    &--large {
      flex-direction: column;

      .review-card__info {
        padding: 17px 0;
      }
      // grid-column: -3 / -1;
    }
  }

  .review-card__title {
    a {
      font-size: 23px;
    }
  }

  .review-card__info {
    padding: 17px 0;
  }
}
